import { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import { api } from "../App";
import Dimmer from "../components/Dimmer";
import axios from "axios";

export default function Page(props) {

    const [data, setData] = useState({})
    const [loading, setLoading] = useState(true)
    const [title, setTitle] = useState("")
    const contentDiv = useRef(null);

    const { id } = useParams();

    useEffect(() => {
        api.get("/pages/" + id).then(res => {
            setData(res.data);
            setLoading(false);
            setTitle(`${res.data?.card?.section?.title} - ${res.data?.card?.title} - ${res.data?.title}`);
            const div = document.createElement('div');
            div.innerHTML = res.data.content;
            contentDiv.current.appendChild(div);

            // if there is an h1 with the exact text: "Content is fetched from" in the div
            if (div.querySelector('h1')?.innerText.toString().trim().toLowerCase().includes("content is fetched from")) {
                // use axios to fetch the content from the url in the h1 anchor tag
                axios.get(div.querySelector('h1 a').href).then(res => {
                    // if the request is successful
                    if (res.status.toString().startsWith('2')) {
                        // set the innerHTML of the div to the response data
                        div.innerHTML += res.data;
                    }
                }).catch(err => {
                    // console.log(err);
                });
            }
        }).catch(err => {
            // console.log(err);
        });
    }, [id])

    useEffect(() => {
        document.title = title;
    }, [title])

    const css = `
    .toastui-editor-ww-code-block-highlighting::after {
        background-image: unset !important;
        background-color: #ffee00 !important;
        padding: 0px 10px !important;
    }
    `

    return (
        <>
            <Dimmer style={{
                '--tw-bg-opacity': '1',
            }} >
                <div className="max-w-7xl mx-auto">
                    <h1 className="text-3xl md:text-4xl lg:text-5xl font-bold text-base-content mb-5">
                        {loading ?
                            <div role="status" className="animate-pulse">
                                <div className="h-2.5 bg-base-content rounded-full w-full mb-4"></div>
                            </div>
                            : title}
                        <hr className="border-base-content" />
                    </h1>
                    <style>{css}</style>
                    <article ref={contentDiv} id="content" className="mb-5 prose max-w-full
                                        prose-sm md:prose-md xl:prose-lg
                                        prose-p:m-0
                                        prose-hr:m-0
                                        prose-code:bg-slate-500
                                        prose-code:bg-opacity-30
                                        prose-a:link-hover
                                        prose-a:link-primary
                                        font-bold
                                        tajawal
                                    "
                    >
                        {loading &&
                            <div role="status" className="max-w-3xl animate-pulse">
                                <div className="h-2 bg-base-content mb-2.5"></div>
                                <div className="h-2 bg-base-content mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[330px] mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[300px] mb-2.5"></div>
                                <div className="h-2 bg-base-content mb-2.5"></div>
                                <div className="h-2 bg-base-content mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[330px] mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[300px] mb-2.5"></div>
                                <div className="h-2 bg-base-content mb-2.5"></div>
                                <div className="h-2 bg-base-content mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[330px] mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[300px] mb-2.5"></div>
                                <div className="h-2 bg-base-content max-w-[360px]"></div>
                            </div>
                        }
                    </article>
                </div>
            </Dimmer>
        </>
    );
}